.news-ticker {
  position: absolute;
  top: 20px;
  right: 0px;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: white;
  text-shadow: 0 0 25px rgba(0, 0, 0, 0.4);
  font-weight: 300;
  width: 240px;
  z-index: 1;
  padding-right: 10px; }
  .news-ticker .content {
    transition: opacity 300ms;
    font-size: 1em; }
    .news-ticker .content .source {
      padding-top: 5px;
      font-size: 0.8em;
      opacity: 0.8;
      text-align: right; }
  .news-ticker .weather-icon {
    width: 25px;
    padding-right: 10px; }

.news-viewer-container-container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  background: rgba(0, 0, 0, 0.6);
  z-index: 2; }
  .news-viewer-container-container .news-viewer-container {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center; }
    .news-viewer-container-container .news-viewer-container .iframe {
      width: 80%;
      height: 80%;
      background: white;
      -webkit-overflow-scrolling: touch;
      overflow-y: scroll; }
      .news-viewer-container-container .news-viewer-container .iframe iframe {
        width: 100%;
        height: 100%;
        outline: none;
        border: none; }
