.ticker {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: white;
  text-shadow: 0 0 25px rgba(0, 0, 0, 0.4);
  margin-top: -20px;
  font-weight: 300;
  min-height: 30px;
  max-height: 30px;
  z-index: 1; }
  .ticker .content {
    transition: opacity 300ms;
    font-size: 1.2em;
    display: flex;
    align-items: center;
    justify-content: center; }
  .ticker .weather-icon {
    width: 25px;
    padding-right: 10px; }
  .ticker.large-text .content {
    font-size: 1.6em; }
