.time-container {
  z-index: 1;
  display: flex;
  align-items: baseline;
  min-height: 200px;
  position: relative; }
  .time-container .time {
    z-index: inherit;
    font-size: 11em;
    font-weight: 300;
    color: white;
    text-shadow: 0 0 25px rgba(0, 0, 0, 0.4); }
  .time-container .show-alarm {
    position: absolute;
    width: 40px;
    height: 40px;
    top: 10px;
    right: -20px;
    color: white;
    opacity: 1; }
